// коллекция пород
function fetchBreeds() {
    return fetch(`${LIST_URL}`)
    .then(resp => {
        console.log(resp);
        if(!resp.ok){
            throw new Error(resp.statusText)
        }
        // return []
        return resp.json()
    })
    .catch(err => console.error(err))
}

